// Import dependencies and classes
import Airplane from "./airplane";
import AirspeedAnalog from "./airspeed_analog";
import { lerp } from "./utils/math";

// Initialize the airplane object
const airplane = new Airplane();

// Create an instance of the AirspeedAnalog class
const airspeedIndicator = new AirspeedAnalog({ parentElement: document.body });

// Add an event listener to the airspeed slider
const airspeedSlider = document.getElementById("airspeedSlider");
airspeedSlider.addEventListener("input", () => {
  const newAirspeed = parseFloat(airspeedSlider.value);
  airplane.setAirspeed(newAirspeed);
  airspeedIndicator.setNeedle(newAirspeed);
});
